@import './face-mesh-view.css';

:root {
  font-family: Inter, Avenir, Helvetica, Arial, sans-serif;
  font-size: 16px;
  line-height: 24px;
  font-weight: 400;

  color-scheme: light dark;
  color: rgba(255, 255, 255, 0.87);
  background-color: #242424;

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-text-size-adjust: 100%;
}

a {
  font-weight: 500;
  color: #646cff;
  text-decoration: inherit;
}
a:hover {
  color: #535bf2;
}

body {
  margin: 0;
  display: flex;
  place-items: center;
  min-width: 320px;
  min-height: 100vh;
}

h1 {
  font-size: 3.2em;
  line-height: 1.1;
}

button {
  border-radius: 8px;
  border: 1px solid transparent;
  padding: 0.6em 1.2em;
  font-size: 1em;
  font-weight: 500;
  font-family: inherit;
  background-color: #1a1a1a;
  cursor: pointer;
  transition: border-color 0.25s;
}
button:hover {
  border-color: #646cff;
}
button:focus,
button:focus-visible {
  outline: 4px auto -webkit-focus-ring-color;
}

.boxContainer {
  position: absolute;
  overflow: hidden;
  width: 100vw;
  height: 100vh;
}

.cameraSwitchButton {
  position: absolute;
  width: 95px;
  height: 70px;
  bottom: 60px;
  right: 40px;
  border-radius: 50%;
  border-color: transparent;
  color: black;
  background-color: white;
}

.cameraToggleButton {
  position: absolute;
  bottom: 60px;
  left: 40px;
  border-color: transparent;
  color: black;
  background-color: white;
}

canvas {
  -webkit-transform: scaleX(-1);
  transform: scale(-1, 1);
  position: absolute;
}

.checklistContainer {
  display: flex;
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  position: absolute;
  top: 40px;
  left: 15px;
  right: 15px;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.5);
}

.close {
  text-align: left;
  color: white;
  padding-right: 5px;
  padding-top: 10px;
  cursor: pointer;
}

.condition {
  color: white;
  font-weight: bold;
}

.conditionContainer {
  width: 50%;
  padding: 5px 0;
}

.conditionSection {
  color: orange;
  margin-bottom: 5px;
  font-weight: bold;
  margin-top: 5px;
  flex: 1;
  min-width: 100%;
}

.countdown {
  display: block;
  text-align: center;
  color: white;
  text-shadow: -1 0 20px black ;
}

.face-mesh-container {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0px;
  top: 0px;
  z-index: 0;
  overflow: hidden;
}

.icon {
  height: 15px;
  width: 15px;
}

.resultContainer {
  position: absolute;
  top: 40px;
  left: 15px;
  right: 15px;
  height: calc(100vh - 75px);
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  overflow: scroll;
}

.screen {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.section {
  color: orange;
  margin-bottom: 5px;
  font-weight: bold;
  margin-top: 15px;
}

.startButton {
  position: absolute;
  width: 100px;
  height: 100px;
  bottom: 40px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 100px;
  background-color: orange;
  z-index: 1;
}

.startButton:disabled {
  background-color: gray;
}

.statusContainer {
  position: absolute;
  width: 100px;
  height: 100px;
  bottom: 30px;
  justify-content: center;
  align-items: center;
  padding: 10px;
  border-radius: 100px;
  border-color: transparent;
}

.text {
  display: block;
  text-align: center;
  color: white;
  text-shadow: -1 0 20px black ;
}

video {
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 100%;
}

.videoContainer {
  width: 100vw !important;
  height: 100vh !important;
}

.vitalSign {
  display: flex;
  flex-direction: row;
  width: 100%;
}

.vitalSignName {
  color: white;
  margin-bottom: 2px;
}

.vitalSignValueView {
  flex: 1;
}

.vitalSignValue {
  text-align: right;
  color: white;
  font-weight: bold;
}

.error {
  display: inline-block;
  position: absolute;
  font-size:xx-large;
  font-weight: bold;
  color: red;
  border-style: solid;
  border-width: 0.2em;
  padding: 1em;
  line-height: normal;
}

@media (prefers-color-scheme: light) {
  :root {
    color: #213547;
    background-color: #ffffff;
  }
  a:hover {
    color: #747bff;
  }
  button {
    background-color: #f9f9f9;
  }
}
